.header {
  $this: &;
  position: relative;
  z-index: 10;
  &__inner {
    @include flex(flex-start, center); }
  &__top {
    background-color: $white;
    position: relative;
    z-index: 1;
    @include lg {
      display: none; }
    #{$this}__inner {
      height: $header-top-height; } }
  &__bottom {
    background-color: $accent;
    background-image: linear-gradient( to top, rgb(48,42,42) 0%, rgb(71,54,41) 100%);
    box-shadow: 0px 0px 25px 0px rgba(7, 6, 3, 0.5);
    position: relative;
    color: $white;
    z-index: 0;
    #{$this}__inner {
      padding-top: 5px;
      padding-bottom: 5px;
      height: $header-bottom-height; } }

  &__top-left,
  &__top-right {
    width: 50%;
    @include flex(flex-start, center); }
  &__top-left {
    @include xl {
      width: 60%; }
    #{$this}__contact {
      width: 33.33333%;
      padding-right: 10px; } }

  &__top-right {
    justify-content: flex-end;
    @include xl {
      width: 40%; }
    #{$this}__contact {
      margin-right: 75px; } }
  &__nav {
    flex-grow: 1;
    text-align: right;
    @include lg {
      position: fixed;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      z-index: 10;
      background-color: $accent;
      background-image: linear-gradient( to top, rgb(48,42,42) 0%, rgb(71,54,41) 100%);
      overflow: auto;
      padding: 15px;
      @include vertical; } }
  &__nav-in {
    @include lg {
      display: inline-block;
      vertical-align: middle;
      font-size: $fz; }
    @include sm {
      font-size: $fz-sm-px; } }
  &__logo {
    font-size: 0; }
  &__burger {
    display: none;
    margin-left: 15px;
    @include lg {
      display: block; } }
  &__lang-mob {
    display: none;
    flex-grow: 1;
    text-align: right;
    @include lg {
      display: block; } }
  &__contact-mob {
    display: none;
    width: 50%;
    margin-bottom: 15px;
    .social {
      justify-content: center; }

    @include lg {
      display: flex;
      justify-content: center; }
    @include sm {
      width: 100%; } }
  &__contacts-mob {
    display: none;
    @include lg {
      @include flex(flex-start, center, row, wrap); } }

  &--fixed {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    #{$this}__bottom {
      // position: fixed
      // top: 0
      // left: 0
      // right: 0
      // z-index: 10
      #{$this}__inner {
        height: $header-bottom-height-fixed; } }
    #{$this}__logo {
      width: 100px; } } }

.header-contact {
  @include flex(flex-start, center);
  font-size: $fz-sm;
  &__icon {
    margin-right: 5px;
    font-size: 0;
    .icon {
      font-size: 14px; } }
  &__text {
    a {
      @include tr(color .3s);
      @include hover {
        color: $accent; } } } }

.lang {
  text-transform: uppercase;
  @include font-title;
  li {
    display: inline-block; }
  a {
    @include tr(color .3s);
    @include hover {
      color: $accent; }
    &.is-active {
      color: $accent; } } }

.burger {
  display: block;
  width: 28px;
  height: 28px;
  position: relative;
  span {
    display: block;
    &, &::after, &::before {
      width: 100%;
      height: 2px;
      background-color: currentColor;
      backface-visibility: hidden;
      border-radius: 2px;
      @include tr(transform .3s); }
    &::after, &::before {
      content: "";
      position: absolute;
      left: 0; }
    &::after {
      top: 5px; }
    &::before {
      bottom: 5px; } }
  &--active {
    span {
      background-color: transparent;
      &::before {
        transform: rotate(45deg) translate(-7px,-5px); }
      &::after {
        transform: rotate(-45deg) translate(-6px,4px); } } } }

