body {
  background: $white;
  line-height: 1.4;
  font-size: $fz;
  min-width: 320px;
  @include font-content;
  color: $c-text;
  @include no-scroll;
  @include sm {
    font-size: $fz-sm-px; } }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.lazy {
  $this: &;
  &--loaded:not(#{$this}--no-fade-in) {
    animation: fadeIn 1s; } }

.container {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px; }

.logo {
  display: inline-block;
  font-size: 0; }

.date-label {
  display: inline-block;
  border-top: 1px solid  $accent;
  border-bottom: 1px solid  $accent;
  text-transform: uppercase;
  @include font-title;
  font-size: em(18);
  padding: .3em .3em .2em;
  position: relative;
  &::before, &::after {
    content: '';
    position: absolute;
    top: 0;
    height: 10px;
    width: 1px;
    z-index: 1;
    background-color: $accent; }
  &::before {
    left: 0; }
  &::after {
    right: 0; }
  &__title {
    &::before, &::after {
      content: '';
      position: absolute;
      bottom: 0;
      height: 10px;
      width: 1px;
      z-index: 1;
      background-color: $accent; }
    &::before {
      left: 0; }
    &::after {
      right: 0; } } }

.list {
  text-transform: uppercase;
  color: $c-title;
  @include font-title;
  font-size: em(24);
  li {
    @include list-bullet;
    &::before {
      margin-right: 1em; }
    @include notlast {
      margin-bottom: .5em; } } }

.link {
  text-transform: uppercase;
  color: $accent;
  @include font-title;
  font-weight: 300;
  letter-spacing: 0.1em;
  @include hover {
    text-decoration: underline; }
  &--arrow {
    display: inline-block;
    position: relative;
    @include after {
      @include icon-arrow(.5, .5, .05, currentColor, -45, em);
      display: inline-block;
      margin-left: .5em;
      position: relative;
      top: -.2em; } } }

.scroll-top {
  position: fixed;
  z-index: 10;
  bottom: 20px;
  right: 20px;
  color: $accent;
  text-transform: uppercase;
  font-size: em(21);
  @include font-title;
  font-weight: 700;
  letter-spacing: .1em;
  width: 50px;
  height: 130px;
  text-align: left;
  padding-top: 40px;
  @include hover {
    .icon {
      transform: translate(-10px, 0); } }

  &__title {
    display: block;
    @include rotate(-90); }
  &__icon {
    display: block;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0) rotate(90deg);
    font-size: 0;
    .icon {
      font-size: 35px;
      @include tr(transform .3s); } } }

