.faq {}


.question {
  background-color: $white;
  position: relative;
  box-shadow: 0px 0px 10px 0px rgba(1, 0, 0, 0.09);
  &__btn {
    display: block;
    width: 100%;
    @extend .title;
    @extend .title--h5;
    padding: 50px 50px 50px 85px;
    text-align: left;
    position: relative;
    @include before {
      @include icon-arrow(10, 10, 1, $accent, -45);
      position: absolute;
      z-index: 1;
      top: 50px;
      right: 30px; }
    &--active {
      padding-bottom: 20px;
      @include before {
        transform: rotate(135deg); } } }
  &__icon {
    color: $gray-lightest;
    position: absolute;
    z-index: 1;
    top: 30px;
    left: 30px;
    .icon {
      font-size: em(45); } }
  &__content {
    display: none;
    padding: 0 50px 50px 85px;
    &--open {
      display: block; } } }
