$grid-gap: 30px;
 :root {
  --grid-gap: 30px; }

$half-gap: calc(var(--grid-gap, #{$grid-gap}) / 2);
$half-gap-negative: calc(var(--grid-gap, #{$grid-gap}) / -2);
$gap: var(--grid-gap, $grid-gap);
$gap-negative: calc(0px - var(--grid-gap, #{$grid-gap}));

.grid {
  margin-left: -$grid-gap / 2;
  margin-right: -$grid-gap / 2;
  margin-bottom: -$grid-gap;
  margin-left: $half-gap-negative;
  margin-right: $half-gap-negative;
  margin-bottom: $gap-negative;
  @include flex(flex-start, normal, row, wrap); }

._col {
  width: 100%;
  padding-left: $grid-gap / 2;
  padding-right: $grid-gap / 2;
  margin-bottom: $grid-gap;
  padding-left: $half-gap;
  padding-right: $half-gap;
  margin-bottom: $gap; }

@for $i from 1 through 12 {
  ._col-#{$i} {
    @extend ._col;
    width: percentage($i/12); }
  ._col-xss-#{$i} {
    @extend ._col;
    @include xssmin {
      width: percentage($i/12); } }
  ._col-xs-#{$i} {
    @extend ._col;
    @include xsmin {
      width: percentage($i/12); } }
  ._col-sm-#{$i} {
    @extend ._col;
    @include smmin {
      width: percentage($i/12); } }
  ._col-md-#{$i} {
    @extend ._col;
    @include mdmin {
      width: percentage($i/12); } }
  ._col-lg-#{$i} {
    @extend ._col;
    @include lgmin {
      width: percentage($i/12); } }
  ._col-xl-#{$i} {
    @extend ._col;
    @include xlmin {
      width: percentage($i/12); } }
  ._offset-#{$i} {
    margin-left: percentage($i/12); }
  ._offset-xss-#{$i} {
    @include xssmin {
      margin-left: percentage($i/12); } }
  ._offset-xs-#{$i} {
    @include xsmin {
      margin-left: percentage($i/12); } }
  ._offset-sm-#{$i} {
    @include smmin {
      margin-left: percentage($i/12); } }
  ._offset-md-#{$i} {
    @include mdmin {
      margin-left: percentage($i/12); } }
  ._offset-lg-#{$i} {
    @include lgmin {
      margin-left: percentage($i/12); } }
  ._offset-xl-#{$i} {
    @include xlmin {
      margin-left: percentage($i/12); } } }

