.nav {
  @include font-title;
  li {
    display: inline-block;
    @include notfirst {
      margin-left: 50px;
      @include lg {
        margin-left: 0; } }
    @include lg {
      display: block;
      margin-bottom: 1em; } }
  a {
    @include tr(color .3s);
    @include hover {
      color: $accent; } }

  &--footer {
    @include font-content;
    li {
      display: block;
      @include notfirst {
        margin-left: 0; }
      @include notlast {
        margin-bottom: em(10); } } } }
