.sponsor {
  box-shadow: 0px 0px 10px 0px rgba(1, 0, 0, 0.09);
  background-color: $white;
  padding: 30px;
  @include lg {
    padding: 20px; }
  @include md {
    padding: 15px; }
  &__top {
    @include flex(space-between, center);
    margin-bottom: 15px; }
  &__title {
    margin-bottom: 15px; }
  &__text {
    margin-bottom: 15px; }
  &__logo {
    width: 50%;
    padding-right: 10px; }
  &__homepage {
    color: $blue;
    text-decoration: underline;
    @include flex(center, center);
    text-align: center;
    width: 50%;
    padding-left: 10px; } }
