.section {
  position: relative;
  padding-top: 100px;
  padding-bottom: 100px;
  @include xl {
    padding-top: 80px;
    padding-bottom: 80px; }
  @include lg {
    padding-top: 60px;
    padding-bottom: 60px; }
  @include md {
    padding-top: 40px;
    padding-bottom: 40px; }
  @include sm {
    padding-top: 30px;
    padding-bottom: 30px; }
  &__title {
    margin-bottom: 40px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 15px; } }
  &__side-title {
    background-color: $gray-lightest;
    padding-bottom: 20px; }
  &__img {
    @include cover;
    height: 100%;
    position: relative;
    background-color: $gray-light;
    iframe {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0; } }

  &__img-wrap {
    font-size: 0;
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } }

  &__side-content, &__side-title {
    padding-left: percentage(2/12); }
  &__side {
    position: relative;
    @include before {
      position: absolute;
      background-color: $gray-lightest;
      top: 0;
      right: 0;
      left: 0;
      z-index: 0;
      top: -100px;
      bottom: 100%;
      @include xl {
        top: -80px; }
      @include lg {
        top: -60px; }
      @include md {
        top: -40px; }
      @include sm {
        top: -30px; } } }
  &__btn {
    text-align: center;
    padding-top: 40px; }
  &__block {
    @include notlast {
      margin-bottom: 80px;
      @include xl {
        margin-bottom: 60px; }
      @include lg {
        margin-bottom: 40px; }
      @include md {
        margin-bottom: 20px; } } }



  &--color {
    background-color: $accent;
    @include image('bg-color.jpg');
    @include cover;
    color: $white; }
  &--border {
    border-top: 1px solid $gray-lightest;
    border-bottom: 1px solid $gray-lightest; }
  &--gray {
    background-color: #eeeeee; } }

.section-btns {
  max-width: 750px;
  margin-left: auto;
  margin-right: auto;
  @extend .hero__btns;
  margin-top: 30px;
  @include md {
    margin-top: 15px; }
  &__btn {
    @extend .hero__btn; } }
